import Vue from 'vue';
import Component from 'vue-class-component';
import { Action, State } from 'vuex-class';
import { Watch } from 'vue-property-decorator';
import Utils from '~/utils';
import ShareImage from '~/components/share_image/index.vue';

@Component({
    components: {
        'share-image': ShareImage
    }
})
export default class AcquaintancesSelect extends Vue {
    friendTemp: string = null;
    // showSelectList: boolean = false;
    friendSuggestList = [];

    showShareImage = false;
    
    @State(state => state.acquaintanceState.acquaintanceList)
    acquaintanceList;
    
    @State(state => state.acquaintanceState.selectAcquaintanceList)
    selectAcquaintanceList;

    @Action('addAcquaintance')
    addAcquaintance;

    @Action('deleteAcquaintance')
    deleteAcquaintance;

    @Watch('friendTemp')
    watchAmout(newVal, oldVal) {
        this.alterFriendInput();
    } 

    activated() {
        this.showShareImage = false;
        this.initData();
    }

    initData() {
        this.friendTemp = null;
        this.friendSuggestList = [];
        // this.showSelectList = false;
    }

    alterFriendInput() {
        console.log(`friendTemp: ${this.friendTemp}`);
        this.friendSuggestList = []; // 每次输入改变，需要清空数据，重新遍历好友列表
        const inputData = this.friendTemp;
		Utils.searchFrinend(inputData, this.acquaintanceList, this.friendSuggestList);
    }

    clickSelectBtn(fid) {
        console.log(fid);
        if (this.acquaintanceList[fid].isSelected) {
            // Vue.set(this.acquaintanceList[fid], 'isSelected', false);
            // this.acquaintanceList[fid].$set('isSelected', false);
            this.deleteAcquaintance(fid);
        } else {
            // Vue.set(this.acquaintanceList[fid], 'isSelected', true);
            // this.acquaintanceList[fid].$set('isSelected', true);
            this.addAcquaintance({
                toUid: this.acquaintanceList[fid].fid,
                realname: this.acquaintanceList[fid].realname,
                amount: this.acquaintanceList[fid].matchmakerInfo.price,
                headPortrait: this.acquaintanceList[fid].headPortrait
            });
        }
    }
}



